@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.contacto {
    background-image: url("../img/full/encuentra.webp");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;

    @include m.tablet {
        min-height: 40rem;
    }

    .contacto-contenido {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    h2, p {
        color: v.$blanco;
        text-align: center;
    }

    h2 {
        font-weight: v.$black;
        font-size: 4rem;
    }

    .boton-contacto {
        margin: 2rem 0;
        
        @include m.boton(v.$amarillo, block);
        @include m.hoverOscuro(v.$amarillo);
        padding: 1rem 3rem;

        @include m.tablet {
            display: inline-block;
        }
    }

}

